import React, { Component } from 'react';
const exp=require('../data/data.json')
export default class Experience extends Component{
    constructor(props){
        super(props)
        this.state={
            experience:null
        }
    }
    
    componentWillMount(){
        this.setState({experience:exp.experience})
    }
    
    render(){
        const exp=this.state.experience===null? null:
            this.state.experience.map( (row,i)=>(
                <div key={i} className="entry" data-date-from-year="" data-date-from-month="" data-date-to-year="" data-date-to-month=""
				style={{ minHeight:'25px' }}>
					<div className="pointer" style={{top: '1.1rem', left: '-38px'}}></div>
					<div className="dates"> <span className="dateFrom">{row.start}</span>
						<span className="dateSeparator"></span>
						<span className="dateTo">{ row.end }</span>
					</div>
					<div className="content">
						<h5 className="title">
							<p>{row.position}</p><p></p>
						</h5>
						<h6 className="subtitle">
							<p>{row.company_name}</p><p></p>
						</h6>
						<div className="text desc">
							<ul>
								{
								    row.list_responsabilities.map( (sub,a)=>(
                                        <li key={a}>{sub}</li>
                                    ))
								}
							</ul>
                            <p><br/></p><p></p>
								{
								    row.description.map((des, b) => (
                                        <p key={b}>{des}</p>
                                    ))
								}
						</div>
					</div>
				</div>
            ))
        return (<div className="section sectionType6 sectionExperience secSpecialLineFirst">
					<h3 className="title"><span className="ico"></span><span className="txt">Experience</span></h3>
                    <div className="entryContainer">{exp}</div>
                </div>)
    }
}