import React, { Component } from 'react'
import { Window,TitleBar,Toolbar} from 'react-desktop/macOs'
import './assets/normalize.css'
import './assets/creator.css'
import './assets/phantomjs.css'
import './assets/print.css'
import './App.css';

import Experience from './components/experience'
import Skill from './components/skill'
import Frame from './components/frame'

class App extends Component {
  	static defaultProps = {
		color: '#cc7f29',
		theme: 'light'
	};
  render() {
    return (
      <Window color={this.props.color}
			        theme={this.props.theme}
			        chrome
              height="100%"
              width="60%"
              id="print"
              className="windowstyle"
              >
               <TitleBar title="My Custom Resume" controls inset>
                   <Toolbar height="43" horizontalAlignment="center"/>
               </TitleBar>
              <Frame>
                <Experience/>
                <Skill/>
              </Frame>   
      </Window>
    );
  }
}

export default App;
