import React, { Component } from 'react'
import { uniq } from 'lodash'
const skl=require('../data/data.json')


export default class Skill extends Component{
    constructor(props){
        super(props)
        
        this.state={
            skill:null
        }
    }

    componentWillMount(){
        const data = skl.skill
        const types = uniq(data.map(x => x.stype))
        const icons =uniq(data.map(x=>x.icon))
        this.setState({
            skill: data,
            skilltype:types,
            icon:icons
        })
    }
    
    
    render(){
        const sk=this.state.skill===null?null:
        (
            this.state.skilltype===null?null:
            this.state.skilltype.map((row,a)=>(
                <div key={a} className={"section sectionType7 ratingCircle " + this.state.icon[a]}>
    				<h3 className="title"><span className="ico"></span><span className="txt">{row}</span></h3>
    				<div className="entryContainer content">
                        { 
                            this.state.skill.filter(x=>x.stype===row).map((subrow,b)=>(
                            <div key={b} className="entry">
								<div className="pointer" style={{top: '0.8rem', left: '-38px'}}></div>
								<h4 className="descShort">
									<p>{subrow.name}<br/></p>
									<p></p>
                                </h4>
								<div className={"rating rating"+ subrow.stars}>
									<div></div>
									<div></div>
									<div></div>
									<div></div>
									<div></div>
								</div>
							</div>
                            ))
                        }
                    </div>
                </div>
                )
            )
        )

        return(<div>
                {sk}
            </div>
            )
    }
}